import {React} from 'react'
import { useHistory } from 'react-router-dom'
import Beras from '../../../assets/packages.svg'

const Beranda = () =>{
    const history= useHistory()
    return(
        <>
            <div class="page-header mb-4"><h4 class="title">Layanan Sistem Indonesia <i>National Single Window</i></h4></div>                               
            <div className="d-flex flex-row">
                <div style={{paddingTop:'2.7rem', borderRadius:'18px', width:'322px', minHeight:'200px', boxShadow:'0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%)'}}  role="button" onClick={()=>history.push('/ppbj')} className="card px-3 pb-3 clickable">
                    <div className="d-flex flex-row pt-3 pb-2">
                        <div className="d-flex flex-column col-9">
                            <div><h4 style={{color:'#02275d'}}>Sistem PPBJ</h4></div>
                            <p style={{color:'grey'}}>Pemberitahuan Perolehan atau Pengeluaran BKP / JKP</p>
                        </div>
                        <div className="d-flex align-items-end mb-3">
                            <img
                            width="70px"
                            style={{float:'right'}}
                            src={Beras}
                            />                 
                        </div>          
                    </div>
                </div>
            </div>         
        </>
    )
}

export default Beranda